import React, { Component } from 'react';
import CatUxUi from './CatUxUi';
import CatGraph from './CatGraph';
import CatDev from './CatDev';
import CatHome from './CatHome';
import CatFull from './CatFull';
import './App.scss';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

class App extends Component {

  constructor(props){
    super(props);
    this.activate = this.activate.bind(this);
  }

  navSm(){
    var x = window.matchMedia('(max-width:980px)')
    var nav = document.getElementById('nav');
    var burger = document.getElementById('burger');
    var section = document.getElementById('section');
    var isActive = nav.classList.contains('navSmClicked');

    if (x.matches){
      if(isActive){
        nav.classList.remove('navSmClicked');
        section.classList.remove('sectionSmClicked');
        burger.classList.remove('burgerClicked');
      }else{
        nav.classList.add('navSmClicked');
        section.classList.add('sectionSmClicked');
        burger.classList.add('burgerClicked');
      }
    } 
  }

  activate(event){
    let el = event.target;

    document.querySelectorAll('.liFu').forEach(function(r){
      r.classList.remove('active');
    })
    el.classList.add('active');

    console.log("hey je log ca"+el);
  }


  render(){
    return (
      <Router>
        <div className="main">
          
          <nav onClick={this.navSm} >
 
            <div id="burger">
              <span className="burger"></span>
              <span className="burger"></span>
              <span className="burger"></span>
            </div>

              <ul id="nav">
                  <Link to="/portfolio/" ><li onClick={this.activate} id="li1" className="active liFu" > Home </li></Link>

                  <Link to="/portfolio/FullProject" ><li onClick={this.activate} id="li2" className="liFu">Full<br/> Project</li></Link>
                  <Link to="/portfolio/GraphicDesign" ><li onClick={this.activate} id="li3" className="liFu">Graphic Design<br/> Project</li></Link>
                  <Link to="/portfolio/Ux-Ui" > <li onClick={this.activate} id="li4" className="liFu"> Ux - Ui<br/>Project</li> </Link>
                  <Link to="/portfolio/Developer" ><li onClick={this.activate} id="li5" className="liFu">Developer<br/> Project</li></Link>

                  <a href="mailto:irvin.debruyne@gmail.com"><li> Contact Me </li> </a>
                  <a href="https://www.linkedin.com/in/irvin-de-bruyne-280824109" target="_blank"> <li>Linkedin</li></a>
              </ul>
          </nav>

          <section id="section" >
            <Switch>
              
              <Route exact path="/portfolio/">
                <CatHome/>
              </Route>
              <Route exact path="/portfolio/Ux-Ui">
                <CatUxUi/>
              </Route>
              <Route exact path="/portfolio/GraphicDesign">
                <CatGraph/>
              </Route>
              <Route exact path="/portfolio/Developer">
                <CatDev/>
              </Route>
              <Route exact path="/portfolio/FullProject">
                <CatFull/>
              </Route>
              
            </Switch>
          </section>
        </div>
      </Router>
    );
  }
}


export default App;
